import { connect } from 'react-redux'
import { importerClick, importerFilesUpload } from '../actions'
import TaskList from '../components/elements/TaskList'

const mapStateToProps = state => {
    return {
        currentPath: state.importer.currentPath,
        documentsTree: state.importer.documentsTree,
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onImporterClick: slug => {
            dispatch(importerClick("", slug))
        },
        fileUpload: (path, dirId) => {
            return dispatch(importerFilesUpload(path, dirId))
        }
    }
}

const ConnectedTaskList = connect(
    null,
    mapDispatchToProps
)(TaskList)

export default ConnectedTaskList


