import { connect } from 'react-redux'
import { alertShow } from '../actions'
import Preferences from '../components/elements/Preferences'

const mapDispatchToProps = dispatch => {
    return {
        alert: (msg, lvl) => {
            dispatch(alertShow(msg, lvl))
        }
    }
}

const ConnectedPreferences = connect(
    null,
    mapDispatchToProps
)(Preferences)

export default ConnectedPreferences


