import React, { Component } from 'react'
import _ from 'lodash'
import moment from 'moment'
import { Link } from 'react-router-dom'
import classNames from 'classnames'
import Portal from 'react-portal'

import Panel from 'react-bootstrap/lib/Panel'

import Page from './Page'
import ContactList from './ContactList'
import DocumentList from './DocumentList'
import Modal from '../modals/Modal'
import Selector from '../../containers/Selector'
import Loader from './Loader'
import Importer from '../../containers/Importer'

import style from '../../styles/TaskList.sass'
import button from '../../styles/buttons.scss'
import modal from '../../styles/ConfirmModal.sass'

import { VIEW_LIST, ROOT_PATH } from '../../constants'
import { createFakePath } from '../../tools'

import types from '../../constants/taskslist.json' 

const DOCTYPE = 'com.homebook.tasks'

export default class TaskList extends Component {

    constructor(props) {
        super(props)

        this.state = {
            currentData: null,
            list: null,
            err: false,
            message: '',
            openModal: false,
            importerOpen: false,
            docSelect: undefined,
            loading: true
        }

        this.renderElement = this.renderElement.bind(this)
        this.renderList = this.renderList.bind(this)
    }

    componentWillMount() {
        this.getCurrent(this.props.match.params.id)

        if(!this.currentId) {
            this.getList()
        }
    }
    
    componentWillReceiveProps(newProps) {
        this.state.err = false
        this.state.message = ''
        this.currentId = undefined

        this.getCurrent(newProps.match.params.id)
        if(!this.currentId) {
            this.getList()
        }
    }

    getCurrent(id) {
        this.currentId = id

        if(!id) return

        this.getDataElements(id)
        .then(data => {
            if(data.length > 0) {
                const task = data[0]
                this.checkedTask(task)
                .then(() => {
                    this.setState({currentData: task, loading: false})
                })
            } else {
                this.setState({err: true, message: 'Aucune donne', loading: false})
            }
        })
        .catch(err => {
            console.error('Load data', err)
            this.setState({err: true, message: 'Erreur de chargement', loading: false})
        })
    }

    getList() {
        this.getDataElements({"$gt": null})
        .then(list => {
            this.setState({list, loading: false})
        })
        .catch(err => {
            console.error('Load data', err)
            this.setState({err: true, message: 'Erreur de chargement', loading: false})
        })
    }

    async getDataElements(id) {
        const indexRef = await cozy.client.data.defineIndex(DOCTYPE, ['_id'])
        return await cozy.client.data.query(indexRef, {
            selector: {
                "_id": id
            }
        })
    }

    async removeDataElement(doc) {
        return await cozy.client.data.delete(DOCTYPE, doc)
    }

    async checkedTask(task) {
        const infos = types[task.type]

        //check documents
        if(infos.documents) {

            //check to add
            const finalDocsMore = infos.documents.filter(f => f.type === "MORE").map(f => {
                const present = _.find(task.documents, d => d.name === f.name)
                return {
                    name: f.name,
                    toAdd: !present,
                    selected: !!present,
                    path: present && present.path,
                    title: present ? present.name : 'Selectionner un document',
                    more: true
                }
            })
    
            //check wh slug
            const docsSlug = infos.documents.filter(f => f.slug)
            const indexRef = await cozy.client.data.defineIndex('io.cozy.files', ['_id', 'tags'])
            const resRaw = await cozy.client.data.query(indexRef, {
                "selector": {
                    "_id": {
                        $gt: null
                    },
                    "tags": {
                        $in: docsSlug.map(f => f.slug)
                    }
                }
            })
            const res = resRaw.filter(f => !f.trashed)
            const finalDocsSlug = []
            for(const d of docsSlug) {
                let index = res.length-1
                const max = Math.max(d.tail || 1, 1)
                for(let i = 0; i < max; i++) {
                    if(index < 0) break
                    const el = _.findLast(res, o => o.tags[0] === d.slug, index)
                    if(el === undefined) break
                    index = res.indexOf(el)-1
                    
                    const dir = await cozy.client.files.statById(el.dir_id)
                    const dirPath = dir.attributes.path
                    finalDocsSlug.push({
                        name: `${d.name}:${el.name}`,
                        title: el.name,
                        selected: true,
                        path: `${dirPath}/${el.name}`,
                        noRemove: true,
                    })
                }
                if(index === res.length-1) {
                    // Aucun document trouv
                    finalDocsSlug.push({
                        name: `${d.name}: Introuvable`,
                        title: d.name,
                        selected: false,
                        noRemove: true,
                        tag: d.slug
                    })
                }
            }
    
            this.currentDocs = [...finalDocsMore, ...finalDocsSlug]
        }

        //check contacts ??
    }

    add() {
        this.setState({
            openModal: true
        })
    }

    addTask(type, name) {

        cozy.client.data.create(DOCTYPE, {type, name, tasks:[], documents:[], contacts:[]})
        .then(data => {
            const id = data._id
            this.props.history.push(`/du-nouveau/${id}`)
        })

    }

    remove() {
        if(!this.state.currentData) return

        cozy.client.data.delete(DOCTYPE, this.state.currentData)
        .then(() => {
            this.props.history.goBack()
        })

    }

    checkTask(key, checked) {
        let tasks = this.state.currentData.tasks
        if(checked) {
            tasks.push(key)
        } else {
            tasks = tasks.filter(nb => nb !== key)
        }
        tasks = _.uniq(tasks)

        const currentData = {...this.state.currentData, tasks}
        this.sendUpdate(currentData)
    }

    clickDocs(item) {
        if(item.selected && item.path) {
            this.props.history.push(createFakePath(item.path))
        } else {
            if(item.toAdd) {
                this.props.onImporterClick()
                this.setState({docSelect: item})
            } else {
                console.log('Item click', item);
                this.props.onImporterClick(item.tag)
                this.setState({importerOpen: true})
            }
        }
    }

    dropDoc(item, e) {
        e.preventDefault()
        e.stopPropagation()
        if(item.selected) return

        const file = e.dataTransfer.files && e.dataTransfer.files[0]
        if(!file) return
        
        let dir
        cozy.client.files.statByPath(ROOT_PATH)
        .then(d => {
            dir = d
            return this.props.fileUpload([file], dir._id)
        })
        .then(data => {
            if(data.err || data.files.length === 0) throw Error()

            if(item.tag) {
                return cozy.client.files.updateAttributesById(data.files[0].id, {tags:[item.tag]})
            }
            return data.files[0]
        })
        .then(file => {
            const path = dir.path + '/' + file.name
            item.path = path
            item.selected = true
            if(item.more) {
                const newDocs = this.currentDocs.filter(d => d.more && d.path && d.selected).map(d => ({name: d.name, path: d.path}))

                cozy.client.data.updateAttributes(DOCTYPE, this.state.currentData._id, {...this.state.currentData, documents: newDocs})
                .then(newData => {
                    this.state.currentData = newData
                    this.forceUpdate()
                })
            } else {
                item.name = `${item.title}:${file.name}`
                item.title = file.name
                this.forceUpdate()
            }
        })
        .catch((e) => {console.error(e)})

    }

    addDoc(path) {
        if(this.state.docSelect === undefined || !this.state.docSelect.toAdd) return

        this.state.docSelect.path = path
        this.state.docSelect.selected = true

        const newDocs = this.currentDocs.filter(d => d.more && d.path && d.selected).map(d => ({name: d.name, path: d.path}))

        cozy.client.data.updateAttributes(DOCTYPE, this.state.currentData._id, {...this.state.currentData, documents: newDocs})
        .then(newData => {
            this.state.currentData = newData
            this.forceUpdate()
        })
    }

    clearMore(item) {
        if(!item.more || !item.selected) return

        item.path = undefined
        item.selected = false

        const newDocs = this.currentDocs.filter(d => d.more && d.path && d.path !== item.path).map(d => ({name: d.name, path: d.path}))

        cozy.client.data.updateAttributes(DOCTYPE, this.state.currentData._id, {...this.state.currentData, documents: newDocs})
        .then(newData => {
            this.state.currentData = newData
            this.forceUpdate()
        })
    }

    checkContact(item, key, checked) {
        if(item.type && item.type === "MORE") return

        let contacts = this.state.currentData.contacts
        if(checked) {
            contacts.push(key)
        } else {
            contacts = contacts.filter(nb => nb !== key)
        }
        contacts = _.uniq(contacts)

        const currentData = {...this.state.currentData, contacts}
        this.sendUpdate(currentData)
    }
    
    sendUpdate(data) {
        //Send new data to cozy
        cozy.client.data.update(DOCTYPE, data, data)
        .then(newData => {
            this.setState({currentData: newData})
        })
    }

    renderElement() {
        if(!this.currentId || !this.state.currentData) return null

        const currentData = this.state.currentData

        const infos = types[currentData.type]

        return (
            <div className={style.edit}>
                <div className={classNames(style.tasks,"panel","panel-default")}>
                    <div className="panel-heading">Pense bte</div>
                    <div className={classNames("panel-body", style.body)}>
                        {infos.tasks.map((item, key) => {
                            if(typeof item === 'string'){
                                return (
                                    <div key={key}>
                                        <input type="checkbox" checked={currentData.tasks.indexOf(key) !== -1} onChange={e => this.checkTask(key, e.target.checked)}/>
                                        {' '+item}
                                    </div>
                                )

                            } else if(typeof item === 'object') {
                                const key1 = key*1000
                                return (
                                    <div key={key}>
                                        <h4>{item.display}</h4>
                                        {item.tasks.map((item2, key2) => (
                                            <div key={key2}>
                                                <input type="checkbox" checked={currentData.tasks.indexOf((key1 + key2)) !== -1} onChange={e => this.checkTask((key1 + key2), e.target.checked)}/>
                                                {' '+item2}
                                            </div>
                                        ))}
                                    </div>
                                )
                            }
                        })}
                    </div>
                </div>
                {infos.documents && (
                    <div className={classNames(style.docs,"panel","panel-default")}>
                        <div className="panel-heading">Vos documents utiles</div>
                        <div className={classNames("panel-body", style.body)}>
                            <DocumentList
                                noDate
                                view={VIEW_LIST}
                                onSelectItem={() => {}}
                                onRemove={item => this.clearMore(item)}
                                onClickItem={(item, e, key) => this.clickDocs(item)}
                                onDrop={(item, e) => this.dropDoc(item, e)}>
                                {this.currentDocs}
                            </DocumentList>
                        </div>
                    </div>
                )}
                {infos.contacts && (
                    <div className={classNames(style.contacts,"panel","panel-default")}>
                        <div className="panel-heading">Vos contacts utiles</div>
                        <div className={classNames("panel-body", style.body)}>
                            <ContactList view={VIEW_LIST} 
                            onSelectItem={(item, e, key, checked) => this.checkContact(item, key, checked)}>
                                {infos.contacts.map((item, key) => {
                                    const selected = currentData.contacts.indexOf(key) !== -1
                                    if(typeof item === 'string'){
                                        return {company: item, selected}
                                    } else if(typeof item === 'object') {
                                        return {
                                            company: item.name,
                                            telephones: [
                                                {type: "FIXE", phone:item.phone}
                                            ],
                                            selected
                                        }
                                    }
                                })}
                            </ContactList>
                        </div>
                    </div>
                )}
                <Portal closeOnEsc isOpened={this.state.docSelect !== undefined} onClose={() => this.setState({docSelect: undefined})}>
                    <Modal>
                        <Selector files onSelect={path => this.addDoc(path)}/>
                    </Modal>
                </Portal>

                <Portal closeOnEsc isOpened={this.state.importerOpen} onClose={() => this.setState({importerOpen: false})}>
                    <Modal>
                        <Importer onImportDone={files => this.componentWillReceiveProps(this.props)}/>
                    </Modal>
                </Portal>
            </div>
        )
    }

    renderList() {
        if(this.currentId || !this.state.list) return null

        return (
            <div className={style.list}>

                {this.state.list.map((item, key) => {
                    if(!item.type) return null

                    const info = types[item.type]
                    if(info === undefined) {
                        return null
                        removeDataElement(item)
                    }
                    return (
                        <div key={key} className={style.card} onClick={() => this.props.history.push(`/du-nouveau/${item._id}`)}>
                            <div className={style.title}>{info.display}</div>
                            <div className={style.name}>{item.name}</div>
                            <div className={style.sub}>{item.tasks.length} / {info.maxTasks}</div>
                        </div>
                    )
                })}

            </div>
        )

    }

    render() {
            const actionEdit = [
                (<button onClick={() => this.props.history.goBack()} className={classNames(button.button, button.stable)} title="Retour">
                    <i className="ion-ios-arrow-back"></i><span className={button.text}>RETOUR</span>
                </button>),
                (<button onClick={() => {}} className={classNames(button.button, button.default)} title="Sauvegarder">
                    <i className="ion-ios-plus-empty"></i><span className={button.text}>SAUVEGARDER</span>
                </button>),
                (<button onClick={() => this.remove()} className={classNames(button.button, button.assertive)} title="Supprimer">
                    <i className="ion-ios-trash-outline"></i><span className={button.text}>SUPPRIMER</span>
                </button>)
            ]

            const actionList = [
                (<button onClick={() => this.add()} className={classNames(button.button, button.default)} title="Ajouter">
                <i className="ion-ios-plus-empty"></i><span>AJOUTER</span>
                </button>)
            ]
        return (
            <Page title="Du nouveau dans mon logement" actions={this.currentId ? actionEdit : actionList}>
                {this.state.err && (
                    <div className="error">
                        {this.state.message}
                    </div>
                )}

                <Loader display={this.state.loading}/>

                {this.renderElement()}
                {this.renderList()}

                {this.state.list && this.state.list.length === 0 && (
                    <div>Aucun lments</div>
                )}

                <Portal isOpened={this.state.openModal} onClose={() => this.setState({openModal: false})}>
                    <ModalCreateTask onAdd={(type, value) => this.addTask(type, value)}/>
                </Portal>

            </Page>
        )
    }
}

export class ModalCreateTask extends Component {
    constructor(props) {
        super(props)

        this.state = {
            value: '',
            type: '',
            warning: false
        }
    }

    render() {

        return (
            <Modal>
                <div className={modal.main}>
                    <div className={modal.content}>
                        <h4>Type d'vnement :</h4>
                        <div className={style.list} style={{maxWidth: '440px'}}>
                            {_.map(types, (item, key) => (
                                <div key={key} className={classNames(style.card, {[style.selected]: this.state.type === key})} onClick={() => this.setState({type: key})}>
                                    <div className={style.title}>{item.display}</div>
                                </div>
                            ))}
                        </div>
                        <div>
                            <h4>Nom du nouvel vnement :</h4>
                            <input
                                type="text"
                                style={{width: '100%'}}
                                value={this.state.value}
                                onChange={e => this.setState({value: e.target.value})}/>
                        </div>
                    </div>
                    <div className={modal.actions}>
                        <button
                            className={classNames(button.button, button.stable)}
                            onClick={e => this.props.closePortal()}>
                            ANNULER
                        </button>
                        <button
                            onClick={e => {
                                if(this.state.type === '' || this.state.name === '') return this.setState({warning: true})

                                this.props.onAdd(this.state.type, this.state.value)
                                this.props.closePortal()
                            }}
                            className={classNames(button.button, button.default)}>
                            AJOUTER
                        </button>
                    </div>
                    {this.state.warning && (
                        <p style={{color: 'orangered', padding: '5px 20px'}}>Il faut selectionn un type et donne un nom pour crer le nouvel vnement</p>
                    )}
                </div>
            </Modal>
        )
    }
}


